export default function NavBar() {
  const updateOpen = useAppStore((state) => state.updateOpen);
  const dopen = useAppStore((state) => state.dopen);
  // const [stock_options, setStockOptions] = useState([]);

  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar position="fixed">
        <Toolbar>
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="open drawer"
            sx={{ mr: 2 }}
            onClick={() => updateOpen(!dopen)}
          >
            <MenuIcon />
          </IconButton>
          <Typography
            variant="h6"
            noWrap
            component="div"
            sx={{ display: { xs: 'none', sm: 'block' } }}
          >
            Skt Predict
          </Typography>
          {/* <Autocomplete
            id="stock-search"
            freeSolo
            options={stock_options}
            onInputChange={(event, value) => handleSearch(value, setStockOptions)}
            renderInput={(params) => (
              <div>
                <Search>
                <SearchIconWrapper>
                  <SearchIcon />
                </SearchIconWrapper>
                <StyledInputBase
                  {...params}   
                  placeholder="Search…"
                  inputProps={{...(params&& params.inputProps),'aria-label': 'search' }}
                />
                </Search>
              </div>
            )}
          /> */}

           <Search>
            <SearchIconWrapper>
              <SearchIcon />
            </SearchIconWrapper>
            <StyledInputBase
              placeholder="Search…"
              inputProps={{ 'aria-label': 'search' }}
            />
          </Search> 
        </Toolbar>
      </AppBar>
    </Box>
  );
}

